import { Input, Component, OnInit } from '@angular/core';

const colorizer = (colors) => {
  const r = colors[0];
  const g = colors[1];
  const b = colors[2];
  const rgba = a => `rgba(${r},${g},${b},${a})`;
  return {
    backgroundColor: rgba(0.2),
    borderColor: rgba(1),
    pointBackgroundColor: rgba(1),
    pointBorderColor: '#fff',
    pointHoverBackgroundColor: '#fff',
    pointHoverBorderColor: rgba(0.8)
  };
};
@Component({
  selector: 'pa-chart',
  templateUrl: './chart.html',
  styleUrls: ['./chart.scss']
})
export class Chart implements OnInit {
  @Input() data = [];
  @Input() max = 10;
  @Input() label = '';
  @Input() color = [148, 159, 177];

  lineChartLabels = ['00:00', '06:00', '12:00', '18:00', '24:00'];
  lineChartOptions = {
    responsive: false,
    scales: {
      yAxes: [{
        ticks: {
          beginAtZero: true,
          fixedStepSize: 1,
          max: this.max,
        }
      }]
    }
  };
  lineChartLegend = false;
  lineChartType = 'line';
  lineChartColors = [
    colorizer(this.color)
  ];

  ngOnInit() {
    this.lineChartColors = [
      colorizer(this.color)
    ];
  }

}



