import { RouterModule, Routes } from '@angular/router';
import { MainContainer } from './main/main.container';
const rs: Routes = [
  {
    path: 'main',
    component: MainContainer
  },
  {
    path: '**',
    redirectTo: '/main'
  }
];

export const Routing = RouterModule.forRoot(rs);



