import { Component, Input } from '@angular/core';

@Component({
  selector: 'pa-intake-button',
  templateUrl: './intake-button.html',
  styleUrls: ['./intake-button.scss']
})
export class IntakeButton {
  @Input() level = 0;

  get label() {
    return `Level ${this.level}`;
  }
}



