import { Component } from '@angular/core';

function getRandomTriage() {
  return Math.floor(Math.random() * 5) + 1;
}

function dater(back: number) {
  const minute = 60 * 1000;
  const delta = back * minute;
  const date = new Date(Date.now() - delta);
  const options = {
    weekday: 'short', month: 'short',
    day: 'numeric', hour: '2-digit', minute: '2-digit'
  };
  return date.toLocaleTimeString('en-us', options);
}

const range = []
for (let i = 0; i < 14; i++) {
  range.push(i);
}

@Component({
  templateUrl: './main.html',
  styleUrls: ['./main.scss']
})
export class MainContainer {
  max = 7;
  charts = [
    {
      label: '1',
      color: [217, 83, 79],
      data: [0, 1, 3, 2, 2]
    },
    {
      label: '2',
      color: [240, 173, 78],
      data: [1, 1, 2, 4, 3]
    },
    {
      label: '3',
      color: [91, 192, 222],
      data: [2, 4, 3, 5, 2]
    },
    {
      label: '4',
      color: [2, 117, 216],
      data: [4, 3, 5, 3, 2]
    },
    {
      label: '5',
      color: [92, 184, 92],
      data: [0, 0, 4, 3, 1]
    }
  ];

  history = range.map(i => ({
    level: getRandomTriage(),
    date: dater(i * 15)
  }));

}



